'use client';

import Image from 'next/image';
import kentaky from "./images/kentaky.png"
import Macdonalds from "./images/Macdonalds.png"
import BuffaloBurger from "./images/BuffaloBurger.png"
import starbucks from "./images/starbucks.png"
const partners = [
  { name: 'KFC', logo: kentaky, rating: 5 },
  { name: 'Buffalo Burger', logo: BuffaloBurger, rating: 5 },
  { name: 'Macdonald', logo: Macdonalds, rating: 5 },
  { name: 'Starbucks', logo: starbucks, rating: 5 },
];

export default function PartnersSlider() {
  return (
    <div className="w-full max-w-7xl mx-auto mt-10">
      <h2 className="text-center text-2xl font-bold text-purple-600 mb-6">
        Our Partners
      </h2>
      {/* Slider */}
      <div className="flex overflow-x-scroll scrollbar-hide scroll-snap-x snap-mandatory gap-4 px-4">
        {partners.map((partner, index) => (
          <div
            key={index}
            className="flex-shrink-0 snap-center w-64 flex flex-col items-center text-center border rounded-lg p-4 bg-white shadow-md"
          >
            <Image
              src={partner.logo}
              alt={partner.name}
                width={80}
              height={80}
              className="mb-4"
              style={{width:"100%"}}
            />
            <h3 className="text-lg font-semibold text-gray-700">
              {partner.name}
            </h3>
            <p className="text-yellow-500">{'â˜…'.repeat(partner.rating)}</p>
          </div>
        ))}
      </div>
    </div>
  );
}